*{
  max-width:100%;
  margin: 0;
  box-sizing: border-box;
}
nav{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #ffc506;
  align-items: center;
  height:5rem;
}
.menu-icon{
  font-Size:2rem !important;
  color:#fff;
  cursor:pointer;
}
.banner{
  margin-top: 10px;
}
.banner img{
  width:100%;
  height:26rem;
}
.list {
  list-style: none;
  display:flex;
  flex-Direction:row;
  justify-Content:space-evenly;
  margin-Top:3rem;
  padding:0;
}
.list-item{
  display:grid;
  place-Items:center;
  width:50px;
  height:50px;
  border-Radius: 50px;
  background-Color:#000;
}
.list-item a{
  display:grid;
  place-Items:center;
}
.list-item{
  font-size:3rem;
  color:#fff;
}
form{
  text-align: left;
  display:flex;
  flex-direction: column;
  align-items: space-between;
  margin-Top:2rem;
  padding: 1rem;
  background-color: #262525;
}
.form{
  text-align: center;
  margin-Top:2rem;
  padding:1rem;
  background-color: #000;
  color:#fff
}

form label{
  margin-top: .5rem;
}
form input{
  margin-top: .5rem;
  padding:.3rem;
}
input:focus{
  outline:none;
}
textarea{
  height:10rem;
  padding:.8rem;
  margin-top: .5rem;
}
input[type='submit']{
  margin-top: 3rem;
  font-weight: bold;
}

